.header {
  margin-bottom: 58px;
  position: relative;
  z-index: 51;
  background: #fff;
}
.header-dd {
  padding: 38px 0 55px;
  position: absolute;
  left: 0;
  background: #FFFFFF;
  width: 100%;
  z-index: 50;
  display: none;
}
.header-dd.active {
  display: block;
}
.header-dd .header-dd-list {
  padding: 25px 0;
  border-width: 1px 0px;
  border-style: solid;
  border-color: #E4DFDF;
}
.header-dd-link {
  font-size: 18px;
  line-height: 100%;
}
.header-dd-item {
  font-size: 18px;
  line-height: 100%;
  letter-spacing: -0.015em;
  color: #494949;
  margin-bottom: 30px;
}
.header-dd-item:last-of-type {
  margin-bottom: 0;
}
.header__burger {
  position: relative;
  z-index: 10;
  display: block;
  width: 21px;
  height: 15px;
}
.header__burger-wrap {
  border: 1px solid #494949;
  border-radius: 1px;
  padding: 10px 8px;
  cursor: pointer;
  display: none;
}
.header__burger-wrap.active span {
  display: none;
}
.header__burger-wrap.active .header__burger::after {
  bottom: 7px;
  transform: rotate(45deg);
  user-select: none;
}
.header__burger-wrap.active .header__burger::before {
  top: 6px;
  transform: rotate(-45deg);
  user-select: none;
}
.header__burger::before {
  content: "";
  top: 0;
  left: 0;
  position: absolute;
  display: block;
  height: 2px;
  width: 100%;
  transition: all 0.3s ease 0s;
  background-color: var(--main-color);
  border-radius: 2px;
}
.header__burger::after {
  content: "";
  bottom: 0;
  left: 0;
  position: absolute;
  display: block;
  height: 2px;
  width: 100%;
  transition: all 0.3s ease 0s;
  background-color: var(--main-color);
  border-radius: 2px;
}
.header__burger span {
  display: block;
  position: absolute;
  left: 0;
  top: 50%;
  height: 2px;
  width: 100%;
  transform: scale(1) translate(0, -50%);
  transition: all 0.3s ease 0s;
  background-color: var(--main-color);
  border-radius: 2px;
}
