:root {
  --main-color: #494949;
  --white-color: #FFFFFF;
  --red-color: #EC1E22;
  --dark-grey: #7D7D7D;
  --light-gray: #BEBCBC;
  --footer-bg: #2E2E2F;
  --background-border: #50B8CF;
  --base-transition: all .4s ease;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

input:-webkit-autofill {
  -webkit-box-shadow: inset 0 0 0 50px #ffffff;
  -webkit-text-fill-color: var(--main-color);
}

input:-webkit-autofill {
  -webkit-animation-name: autofill;
  -webkit-animation-fill-mode: both;
}

.overlay {
  background: rgba(73, 73, 73, 0.78);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 19;
  display: none;

  &.active {
    display: block;
  }
}

.no-scroll {
  overflow: hidden;
  padding-right: 17px;
}

.btn {
  background: var(--red-color);
  border-radius: 1px;
  border: none;
  font-size: 20px;
  line-height: 100%;
  letter-spacing: -0.015em;
  color: var(--white-color);
  padding: 15px 40px;
  transition: var(--base-transition);

  &:hover {
    background: #CB1519;
  }
}

.section-title {
  margin-bottom: 55px;
  font-size: 40px;
  line-height: 130%;
  letter-spacing: -0.015em;
  color: var(--main-color);
}

.header {
  margin-bottom: 58px;
  position: relative;
  z-index: 51;
  background: #fff;

  &-dd {
    padding: 38px 0 55px;
    position: absolute;
    left: 0;
    background: #FFFFFF;
    width: 100%;
    z-index: 50;
    display: none;

    &.active {
      display: block;
    }

    .header-dd-list {
      padding: 25px 0;
      border-width: 1px 0px;
      border-style: solid;
      border-color: #E4DFDF;
    }

    &-link {
      font-size: 18px;
      line-height: 100%;
    }

    &-item {
      font-size: 18px;
      line-height: 100%;
      letter-spacing: -0.015em;
      color: #494949;
      margin-bottom: 30px;

      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }

  &__burger {
    position: relative;
    z-index: 10;
    display: block;
    width: 21px;
    height: 15px;

    &-wrap {
      border: 1px solid #494949;
      border-radius: 1px;
      padding: 10px 8px;
      cursor: pointer;
      display: none;

      &.active {
        & span {
          display: none;
        }

        .header__burger::after {
          bottom: 7px;
          transform: rotate(45deg);
          user-select: none;
        }

        .header__burger::before {
          top: 6px;
          transform: rotate(-45deg);
          user-select: none;
        }
      }
    }

    &::before {
      content: "";
      top: 0;
      left: 0;
      position: absolute;
      display: block;
      height: 2px;
      width: 100%;
      transition: all 0.3s ease 0s;
      background-color: var(--main-color);
      border-radius: 2px;
    }

    &::after {
      content: "";
      bottom: 0;
      left: 0;
      position: absolute;
      display: block;
      height: 2px;
      width: 100%;
      transition: all 0.3s ease 0s;
      background-color: var(--main-color);
      border-radius: 2px;
    }

    & span {
      display: block;
      position: absolute;
      left: 0;
      top: 50%;
      height: 2px;
      width: 100%;
      transform: scale(1) translate(0, -50%);
      transition: all 0.3s ease 0s;
      background-color: var(--main-color);
      border-radius: 2px;
    }


  }

  &-menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
  }

  &-logo {
    display: flex;
    align-items: center;
    justify-content: center;

    &__caption {
      font-size: 14px;
      line-height: 95.69%;
      letter-spacing: -0.015em;
      color: #A6A5A5;
      margin-left: 19px;
      user-select: none;
    }
  }

  &-option {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &-nav {
    margin-right: 45px;

    &__list {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &__item {
      margin-right: 35px;

      &:last-of-type {
        margin-right: 0;
      }
    }
  }

  &-btn {
    border: 1px solid var(--red-color);
    border-radius: 1px;
    background: transparent;
    padding: 14px 20px;
  }

  .icon.call {
    max-width: 13px;
    max-height: 13px;
    fill: #EC1E22;
    display: none;
  }
}

